@import url('https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&display=swap');


*,
*::before,
*::after {
    padding: 0;
    margin: 0;
}

html,
body {
    font-family: 'Lato', sans-serif;
    width: 100%;
    // height: 100dvh;
     @media (max-width: 900px) {
        font-size: calc(0 * 0vw + 1.035em);
        // overflow-x: hidden;
     }
}

.container {
    max-width: 80em;
    height: 100%;

    @media (max-width: 900px) {
        max-width: 100%;
    }
}


// Layouts 
@import './layouts/variables';
@import './layouts/mixins';
@import './layouts/modal';
@import './layouts/dashboard';
@import './layouts/background';
@import './layouts/bascule';
@import './layouts/formPray';
@import './layouts/pray';
@import './layouts/users';
@import './layouts/mood';
@import './layouts/notification';
@import './layouts/register';
@import './layouts/login';



.app {
    width: $size!important;
   &__container {
      @include between;
      align-items: flex-start;
      max-width: $size!important;
   }
}


.auth {
    @include flex;
    
    &__auth {
        position: relative;
        @include center;
        width: $size;

        @media (max-width: 900px) {
            background: $light;
             height: 100vh;
            //  background: green;
        }
    }

    &__image {
        min-width: 57em;
        height: 100vh;
        background: rgb(139, 139, 255);

        @media (max-width: 900px) {
             display: none;
        }
    }
}

.wel {
    @include center;
    height: 50em;
    // border: 1px solid red;

    &__card {
        padding: 2em 1.5em 1.8em 1.5em;
        border-radius: .8em;
        width: 25em;
        height: auto;
        background: $light;
    }

    &__image {
        position: relative;
        width: $size;
        height: 10em;
    }

    &__img {
        width: $size;
        height: $size;
    }

    &__text {
        padding: 1em .6em;
        padding-top: 1.8em;
    }

    &__title {
        font-weight: 500;
        font-size: 1.3em;
        text-align: center;
        color: black;
    }
}


.invalid {
    position: absolute;
    bottom: 5em;
    padding: 1em 2em;
    border-radius: .3em;
    border: 1px solid lighten($reds, 30);
    color: lighten($reds, 10);
    background: lighten($reds, 47);
    z-index: 100;

    @media (max-width: 900px) {
        position: fixed;
        top: 0;
        left: 0;
        padding: 0;
        @include center;
        border: 0;
        border-radius: 0;
        color: none;
        width: $size;
        height: $size;
        background: rgba(0, 0, 0, 0.48);
   
        &__content {
            padding: 1em;
            border-radius: .3em;
            border: 1px solid lighten($reds, 30);
            color: lighten($reds, 10);
            background: lighten($reds, 37);
        }
    }

}


// For Loading Register 
.loading { 
    position: absolute;
    bottom: 7em;
    bottom: 0;
    // left: 30em;
    left: 0;
    @include center;
    flex-direction: column;
    width: 100%;
    height: 100%;
    background: rgba(255, 255, 255, 0.807);
    cursor: not-allowed;

    &__title {
        margin-top: 2.5em;
        font-size: .8em;
        font-weight: 300;
        color: rgb(139, 139, 255);
    }

    @media (max-width: 900px) {
        position: fixed;
        top: 0;
        left: 0;
        @include center;
        width: $size;
        height: $size;
    }
}