



.basc {
    padding: 2em 0 1.5em 0;
    @include between;

    @media (max-width: 400px) {
        padding: 2em 0 .6em 0;
    }

    &__bascs {
        @include align;
    }

    &__title {
        font-size: 1.5em;
        font-weight: 400;

        @media (max-width: 400px) {
            font-size: 1em;
        }
    }

    &__basc {
        margin-right: 1em;
        color: darken( $light-low, 12);
        cursor: pointer;
        font-size: 1.5em;
        transition: color .3s linear;

        &:hover {
            color: $bl-low;
        }
    }

    &__btn {
        @include resetLink;
        color: darken( $light-low, 12);

        > span {
            color: $bl-low;
        }

        @media (max-width: 500px) {
           font-size: 1.3em;   
        }
    }


}