



.reg {
    // border: 1px solid red;
    border-radius: .5em;
    height: 50em;
    width: 35em;
    

    @media (max-width: 900px) {
        border-radius: 0;
        width: $size;
        height: 0;
        height: 100vh;
    }

    &__form {
        @media (max-width: 900px) {
            // width: $size;
            height: $size;
            // border: 1px solid red;
        }
    }

    &__image {
        margin: 0 auto;
        margin-bottom: 1.7em;
        width: 11em;
        height: 11em;

        @media (max-width: 900px) {
            width: 8em;
            height: 8em;
        }
    }

    &__img {
        width: $size;
        height: $size;
        object-fit: contain;
        object-position: $size $size;
    }

    &__title {
        margin-bottom: 1.6em;
        font-size: 2.5em;
        font-weight: 500;
        text-align: center;

        @media (max-width: 900px) {
            margin-bottom: 1.1em;
            font-size: 2em;
        }
    }

    &__inputs {
        margin-bottom: 1em;
        @include flex;

        @media (max-width: 900px) {
           flex-direction: column;
           margin-bottom: 0;
           @include center;
        }

        @media (max-width: 400px) {
            font-size: calc(0 * 0vw + 5em);
        }
    }

    &__input {
        margin-right: 1.6em;
        padding: 1.11em 1em;
        border: 1px solid lighten($light-low, 15);
        outline: 0;
        border-radius: .2em;
        width: 85%;
        transition: border-color .3s ease-in-out;
        color: #444;

        &:focus {
            border-color: lighten($bl-low, 15);
        }

        @media (max-width: 900px) {
            margin-right: 0;
            margin-bottom: 1em;
        }

        @media (max-width: 400px) {
            font-size: calc(0 * 0vw + 0.2em);
        }
    }

    &__bottom {
        @include between;
        margin-top: 3em;
        width: 95%;

        @media (max-width: 900px) {
            margin: 0 auto;
            margin-top: 2em;
            width: 90%;
        }

        @media (max-width: 503px) {
            flex-direction: column;
            width: 95%;
        }

        @media (max-width: 400px) {
            padding-bottom: 2em;
        }
    }

    &__quest {
        @media (max-width: 900px) {
            font-size: 1em;
        }
        
        @media (max-width: 503px) {
            width: $size;
        }
    }

    &__login__link {
        margin-left: 1em;
        text-decoration: underline;
        color: lighten($bl-low, 10);
        cursor: pointer;
        transition: color .3s ease-in-out;

        &:hover {
            color: lighten($bl-low,0);
        }

    }

    &__btn {
        padding: 1em 1em;
        background-color: lighten($bl-low, 1);
        color: $light;
        font-weight: 900;
        border-radius: .3em;
        border: 0;
        outline: 0;
        width: 14em;
        cursor: pointer;
        transition: opacity .5s ease-in-out;

        &:hover {
            opacity: .8;
        }

        @media (max-width: 503px) {
            margin-bottom: 1em;
            width: $size;
            padding: 1.5em 1em;
            background-color: darken($bl-low,4);
        }

        @media (max-width: 400px) {
            font-size: calc(0 * 0vw + .9em);
        }
    }
}   


.regNone {
    display: none;
}
