




.login {
    // border: 1px solid green;

    // display: none;

    &__image {
        @media (max-width: 503px) {
            width: 7em!important;
            height: 7em!important;
        }
    }

    &__inputs {
        align-items: center;
        flex-direction: column;

        @media (max-width: 503px) {
           width: $size;
        }
    }

    &__input {
        margin-right: 0!important;
        margin-bottom: 1.8em;
        width: calc($size - 9%);

        @media (max-width: 400px) {
            width: 0;
            width: 95%;
            padding-left: 1em;
            padding-right: 0;

            &:last-child {
                margin-bottom: 0;
            }
        }
    }

    &__quest {
        margin-left: 1.6em;
        // font-size: .8em!important;
        // @media (max-width: 503px) {
        // //    margin-left: 0;
        // }
    }

    &__btn {
        width: 11em;

        @media (max-width: 503px) {
            width: $size;
        }
    }

    &__bottom {
        width: $size;
    }
}


.loginNone {
    display: none;
}